<template>
  <div id="home">
    <!-- 搜索框 -->
    <van-sticky>
      <van-search
        placeholder="请输入搜索关键词"
        background="#6e7bd8"
        show-action
        v-model="value"
        @focus="showSelect">
        <div slot="action" @click="onSearch">
          <span v-show="showList" class="search-btn">
            搜索
          </span>
          <span v-show="!showList" class="search-btn">
            取消
          </span>
        </div>
      </van-search>
    </van-sticky>

    <!-- 分类选择 -->
    <transition name="fade">
      <van-row v-show="choose && showList" class="choose-row">
        <van-col span="20" class="choose-tag">
          已选分类：<van-tag plain size="medium">{{choose}}</van-tag>
        </van-col>
        <van-col span="4" class="choose-close" @click="choose = ''">
          <van-icon name="close" />
        </van-col>
      </van-row>
    </transition>

    <!-- 分类搜索 -->
    <transition name="fade">
      <div v-show="!showList">
        <div id="select-choose">选择分类搜索</div>
        <van-row class="select-list" v-for="i in 2" :key="i">
            <van-col
              v-for="j in 3"
              :key="j"
              span="8"
              :class="{
                'van-hairline--right': j !== 3,
                'select-item': true}"
              @click="chooseSelect((i - 1) * 3 + j)"
            >
                分类{{(i - 1) * 3 + j}}
            </van-col>
          </van-row>
      </div>
    </transition>

    <!-- 商品列表和底部tabbar -->
    <transition-group name="fade">
      <!-- 商品列表 -->
      <van-list
        key="list-key"
        v-show="showList"
        class="list-model"
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
      >
        <div v-for="(item, index) in list" :key="index" class="list-item van-hairline--bottom">
          <van-row class="list-item-row">
            <van-col span="13" class="list-item-title van-multi-ellipsis--l2" @click="showGoodsDetail(index)">
              {{item.title}}
            </van-col>
            <van-col span="3" class="list-item-num">
              <div class=" wrap">剩:{{item.left}}</div>
            </van-col>
            <van-col span="4" class="list-item-num">
              <div v-if="!showPrice" class="wrap">￥{{(item.price * 1).toFixed(1)}}</div>
            </van-col>
            <van-col span="4" class="shopping-cart-btn">
              <van-button plain hairline color="#46b8da" icon="shopping-cart-o" />
            </van-col>
          </van-row>
        </div>
      </van-list>

      <!-- 排序 -->
      <div v-show="showList && showSort" key="sort-key" class="sort-model" @blur="showSort = false">
        <ul>
          <li v-for="item in ['新品', '名称', '数量', '价格']" :key="item" class="sort-item van-hairline--bottom">{{item}}</li>
        </ul>
      </div>

      <!-- 底部tabbar -->
      <van-tabbar
        key="tabbar-key"
        v-show="showList"
        v-model="active"
        id="tabbar"
        active-color="#fff"
        inactive-color="#fff"
        @change="onChange">
          <van-tabbar-item name="sort" icon="down" @click="showSortModel">排序</van-tabbar-item>
          <van-tabbar-item name="code" icon="coupon-o">授权码</van-tabbar-item>
          <van-tabbar-item name="hot" icon="hot-o">促销</van-tabbar-item>
          <van-tabbar-item name="delivery" icon="logistics">快递</van-tabbar-item>
          <van-tabbar-item name="shoppingCart" icon="cart-o">购物车</van-tabbar-item>
      </van-tabbar>
    </transition-group>

    <!-- 商品详情弹窗 -->
    <!-- <van-overlay
      :show="showOverlay"
      :z-index="100"
      :custom-style="{'background-color': 'rgba(0, 0, 0, 0.9)'}">
    </van-overlay>
    <transition name="fade">
      <div v-show="showOverlay" class="overlay-contain" @click="hideOverlay">
        <van-image class="goods-image" :src="overlayObj.src" >
          <template v-slot:loading>
            <van-loading type="spinner" size="20" />
          </template>
        </van-image>
        <div class="description" @click.stop>
          {{overlayObj.description}}
        </div>
      </div>
    </transition> -->
    <van-popup v-model="showOverlay" class="overlay-model" @close="hideOverlay">
      <van-image
        class="goods-image"
        :src="overlayObj.src">
        <template v-slot:loading>
          <van-loading type="spinner" size="20" />
        </template>
      </van-image>
      <div class="overlay-title van-hairline--bottom" @click.stop>
        {{overlayObj.title}}<span class="overlay-price">￥ {{overlayObj.price}}</span>
      </div>
      <div class="description" @click.stop>
        {{overlayObj.description}}
      </div>
    </van-popup>

    <!-- 授权码 -->
    <van-popup
      v-model="showCode"
      class="code-model"
      @open=" codeValue = ''"
      @close="resectCodeModel">
      <div class="password-input-model van-hairline--surround">
        <van-password-input
          :value="codeValue"
          :mask="false"
          :length="6"
          :focused="true"
          @focus="showKeyboard=true"
        />
      </div>
      <div v-show="!codeValidate" class="code-tip">{{codeTip}}</div>
      <van-loading v-show="codeValidate" size="24px" class="code-tip">验证中...</van-loading>
    </van-popup>
    <!-- 数字键盘 -->
    <van-number-keyboard
      :show="showKeyboard"
      :z-index="3000"
      @input="onInput"
      @delete="onDelete"
      @blur="showKeyboard=false"
      safe-area-inset-bottom
    />
  </div>
</template>

<script>

export default {
  name: 'home',
  data () {
    return {
      showList: true,
      showOverlay: false,
      showCode: false,
      showKeyboard: false,
      showSort: false,
      choose: '',
      value: '',
      codeValue: '',
      active: 'sort',
      list: [],
      loading: false,
      finished: false,
      sortLoading: false,
      codeValidate: false,
      codeTip: '授权码',
      showPrice: false,
      overlayObj: {
        id: '',
        src: '',
        price: '',
        title: '',
        description: '',
        count: 0,
        left: 0
      }
    }
  },
  methods: {
    onSearch () {
      if (this.showList) {

      } else {
        this.showList = true
      }
    },
    chooseSelect (index) {
      this.showList = true
      this.choose = '分类' + index
    },
    onLoad () {
      let listData = [
        {
          id: 'goods1',
          src: 'http://daji.runcon.cn/_upload/file/2019/07/2019070916284566272878.jpg',
          price: 1234.0,
          title: '高丽雅娜 肉毒去纹眼霜 送按摩仪 【授】【一般贸易】',
          description: '【产品介绍：去黑眼圈 淡化细纹 淡化黑眼圈 滋润紧致 改善眼部暗沉 半透明乳霜质地 延展性好 滋润度高 无惧时光，让眼周肌肤就此冻龄】',
          count: 1,
          left: 10
        },
        {
          id: 'goods2',
          src: 'http://daji.runcon.cn/_upload/file/2019/07/2019070916293813669308.jpg',
          price: 0.5,
          title: '高丽雅娜 SHO 7天涂抹式肉毒杆菌安瓶精华 【授】【一般贸易】',
          description: '淘宝控价价格为【259】  上架最低零售价为【139】【产品介绍：水油平衡  改善皱纹  细致毛孔  修护舒缓  提亮肤色  改善面色 肌肤通透润泽  补水保湿  赋予活力  8大植物成分  滴滴精粹】',
          count: 1,
          left: 10
        },
        {
          id: 'goods3',
          src: 'http://daji.runcon.cn/_upload/file/2019/06/2019061815140404520333.jpg',
          price: 128.0,
          title: '香蒲丽海洋水光眼膜 60片/盒 绿色 【授】【一般贸易】',
          description: '【产品介绍：镇定舒缓，补水紧致，消除浮肿，润泽保湿，去黑眼圈，珍贵深海植萃，天然赋活】',
          count: 1,
          left: 10
        },
        {
          id: 'goods4',
          src: 'http://daji.runcon.cn/_upload/file/2019/07/2019070916304395167195.jpg',
          price: 44.5,
          title: '香蒲丽 黄金水光面膜凝胶 金色补水淡斑排毒 新版防伪',
          description: '【产品介绍：紧致肌肤 修复细纹 补充胶原蛋白 质地柔软 贴合五官 营养完整吸收 提拉亮肤 高倍浓缩成分，维持肌肤年轻状态】',
          count: 1,
          left: 10
        },
        {
          id: 'goods5',
          src: 'http://daji.runcon.cn/_upload/file/2019/07/2019070916314008269196.jpg',
          price: 353.0,
          title: '香蒲丽 绿色眼膜',
          description: '【产品介绍：淡黑眼圈 缓解水肿 温和低敏 改善细纹 密集补水修复 提拉紧致 改善油腻 补水去干纹 黄金凝胶质地 易吸收 不粘腻 珍贵深渊植萃，天然赋活】',
          count: 1,
          left: 10
        },
        {
          id: 'goods6',
          src: 'http://daji.runcon.cn/_upload/file/2019/09/2019092214171071182246.jpg',
          price: 1232.0,
          title: '香港科士威浪漫身体乳润肤乳 200ml 【一般贸易】',
          description: '【产品介绍：补水保湿 细腻肌肤 修护肌肤 滋润 持久留香 白色乳液质地 轻薄柔软 不油腻 易推开 吸收快】',
          count: 1,
          left: 10
        },
        {
          id: 'goods7',
          src: 'http://daji.runcon.cn/_upload/file/2019/07/2019072917344185429279.jpg',
          price: '7',
          title: '飞利浦 5060 剃须刀',
          description: '【产品介绍：干湿双剃 顺滑3刀头 30分钏续航 一机多用 深层防水设计 可直接冲洗 持久耐用】',
          count: 1,
          left: 10
        }
      ]
      // 异步更新数据
      setTimeout(() => {
        this.list.push.apply(this.list, listData)
        // 加载状态结束
        this.loading = false
        this.finished = true
      }, 500)
    },
    showSelect () {
      this.showList = false
    },
    showGoodsDetail (index) {
      this.showOverlay = true
      Object.assign(this.overlayObj, this.list[index])
    },
    hideOverlay () {
      this.showOverlay = false
      this.overlayObj.src = ''
    },
    resectCodeModel () {
      this.active = 'sort'
      this.codeValue = ''
      this.showCode = false
      this.showKeyboard = false
      this.codeTip = '授权码'
    },
    onInput (key) {
      this.codeValue = (this.codeValue + key).slice(0, 6)
      if (this.codeValue.length === 6) {
        // do something
        this.codeValidate = true
        setTimeout(() => {
          if (this.codeValue.length === 6) {
            this.showCode = false
            setTimeout(() => {
              this.$toast('验证成功')
              this.showPrice = true
            }, 500)
          } else {
            this.codeValue = ''
            this.codeTip = '授权码无效'
          }
          this.codeValidate = false
        }, 1500)
      }
    },
    onDelete () {
      this.codeValue = this.codeValue.slice(0, this.codeValue.length - 1)
    },
    showSortModel () {
      this.showSort = !this.showSort
    },
    onChange (index) {
      switch (index) {
        case 'code':
          this.showCode = true
          this.showKeyboard = true
          break
        case 'hot':
        case 'delivery':
        case 'shoppingCart':
          this.$router.push('/' + index)
          break
      }
    }
  },
  watch: {
    showOverlay (val) {
      document.body.style.overflow = val ? 'hidden' : 'auto'
    },
    showCode (val) {
      document.body.style.overflow = val ? 'hidden' : 'auto'
    }
  }
}
</script>

<style lang="less">
#home {
  font-size: 14px;
  height: 100%;
  .search-btn {
    color: white;
  }

  .choose-row {
    height: 50px;
    line-height: 50px;
    text-align: left;
    .choose-tag {
      padding-left: 11px;
    }
    .choose-close {
      text-align: center;
      height: 49.6px;
      line-height: 67px;
      .van-icon-close {
        font-size: 22px;
      }
    }
  }

  .list-model {
    margin-bottom: 48px;
    background: white;
    .list-item {
      .list-item-row {
        padding: 15px 0;
      }
      .list-item-title {
        line-height: 26px;
        text-align: left;
        padding: 0 11px;
      }
      .list-item-num {
        line-height: 26px;
        .wrap {
          word-break: break-all;
          word-wrap: break-word;
        }
      }

      .shopping-cart-btn {
        height: 43px;
        line-height: 43px;
      }

      .van-button--normal {
        padding: 0;
        width: 39px;
        height: 34px;
        line-height: 34px;
      }
    }
  }

  .sort-model {
    position: fixed;
    bottom: 48px;
    left: 13px;
    width: 72px;
    background: white;
    box-shadow: 0px 0px 3px 3px #9697995c;
    .sort-item {
      padding: 8px 0;
    }
  }

  #select-choose {
    margin: 20px 0 10px 0;
    color: #9E9E9E;
  }

  .select-list {
    padding: 15px;
    .select-item {
      color: #072048;
    }
  }

  #tabbar {
    background: rgb(110, 123, 216);
  }

  .overlay-contain {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 101;
    .goods-image {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      img {
        object-fit: contain;
      }
      .van-image__error, .van-image__loading {
        background-color: transparent;
      }
    }
    .description {
      color: white;
      position: absolute;
      left: 0;
      bottom: 0;
      font-size: 15px;
      padding: 10px;
      height: 110px;
      line-height: 30px;
      overflow: scroll;
      text-align: left;
    }
  }

  .overlay-model {
    width: 82%;
    border-radius: 10px;
    .goods-image{
      width: 100%;
      min-height: 300px;
      .van-image__error, .van-image__loading {
        background-color: transparent;
      }
    }
    .overlay-title {
      padding: 5px 12px;
      line-height: 26px;
      text-align: left;
    }
    .overlay-price {
      float: right;
      margin-right: 5px;
      color: #F44336;
    }
    .description{
      padding: 13px 12px;
      line-height: 28px;
      text-align: left;
    }
  }

  .code-model {
    width: 85%;
    height: 180px;
    top: 35%;
    .password-input-model {
      margin: 49px 16px 0 16px;
      .van-password-input {
        margin: 0;
      }
    }
    .code-tip {
      margin-top: 26px;
    }
    .van-loading__text {
      color: #2c3e50;
    }
  }
}
.fade-enter-active {
  transition: opacity .5s;
}
.fade-enter {
  opacity: 0;
}
</style>
